import { Component, Input, Output, EventEmitter } from "@angular/core";


@Component({
  selector: "new-textbox-input",
  // template: `
  //   <div class="newTextBox">
  //     <ion-item no-lines no-padding>
  //       <ion-label stacked>{{label}}</ion-label>
  //       <ion-input
  //         [ngModel]="value"
  //         (ngModelChange)="modelChange($event)"
  //         type="{{type}}"
  //         placeholder="{{placeholder}}"
  //         maxlength="{{maxlength}}"
  //         (keyup)="keyup($event)"
  //         (change)="change($event)"
  //       ></ion-input>
  //     </ion-item>
  //   </div>
  // `,
  template: `
    <div class="newTextBoxFloating">
      <ion-item no-lines no-padding [ngStyle]="{'background-color': disabled ? '#D8DCE8' : '#fff'}">
        <ion-label floating>{{label}}</ion-label>
        <ion-input
          [ngModel]="value"
          (ngModelChange)="modelChange($event)"
          type="{{type}}"          
          maxlength="{{maxlength}}"
          (keyup)="keyup($event)"
          (change)="change($event)"
          (blur)="blur($event)"
          (focus)="focus($event)"
          [disabled]="disabled"
        ></ion-input>
      </ion-item>
      <small style="display: block;margin-top:5px" *ngIf="warnText">{{warnText}}</small>
    </div>
  `,
})

export class NewTextboxInputComponent {
  @Input() label:string;
  @Input() type:string;
  @Input() maxlength:string;
  @Input() placeholder:string;
  @Input() currency:string;
  @Input() disabled:boolean=false;
  @Input() value: any;
  @Output() valueChange: EventEmitter<any> = new EventEmitter();
  @Output() onKeyup: EventEmitter<any> = new EventEmitter();
  @Output() onChange: EventEmitter<any> = new EventEmitter();
  @Output() onBlur: EventEmitter<any> = new EventEmitter();
  @Output() onFocus: EventEmitter<any> = new EventEmitter();

  @Input() warnText : string;

  constructor() {}

  keyup(event) {
    this.onKeyup.emit(event);
  }

  change(event) {
    this.onChange.emit(event);
  }

  modelChange(event) {
    this.value = event;
    this.valueChange.emit(this.value);
  }

  blur(ev){
    this.onBlur.emit(ev);
  }

  focus(ev){
    this.onFocus.emit(ev)
  }
}


